<template>
  <div class="led-control">
    <color-picker
      class="led-control-picker"
      v-bind="color"
      variant="persistent"
      :mouse-scroll="true"
      :step="10"
      @input="onColorChange"
      @change="setColor"
    />
    <div class="led-control-sliders">
      <h2>Sättigung</h2>
      <ui-slider v-model="color.saturation" :interval="10" :step="5" :snapToSteps="true" :showMarker="true" @change="onSaturationChange" />
      <h2>Helligkeit</h2>
      <ui-slider v-model="color.luminosity" :interval="10" :step="5" :snapToSteps="true" :showMarker="true" @change="onLuminosityChange" />
      <h2>An/Aus</h2>
      <ui-switch v-model="on" @change="onStateChange" />
    </div>
  </div>
</template>

<script>
import ColorPicker from '@radial-color-picker/vue-color-picker'
import Convert from 'color-convert'
import { UiSwitch, UiSlider } from 'keen-ui'
import 'keen-ui/dist/keen-ui.css'
import '@radial-color-picker/vue-color-picker/dist/vue-color-picker.min.css'

const host = 'http://light:5000'

export default {
  name: 'led-control',

  components: {
    ColorPicker,
    UiSwitch,
    UiSlider
  },

  data () {
    return {
      on: true,
      color: {
        hue: 50,
        saturation: 100,
        luminosity: 50
      }
    }
  },

  mounted () {
    this.$http.get(`${host}/info`)
      .then(rawResult => {
        let result = JSON.parse(rawResult.bodyText)
        let colors = Convert.rgb.hsl([result.r, result.g, result.b])
        this.color.hue = colors[0]
        this.color.saturation = colors[1]
        this.color.luminosity = colors[2]
      })
  },

  methods: {
    setColor () {
      let rgb = Convert.hsl.rgb([
        this.color.hue,
        this.color.saturation,
        this.color.luminosity
      ])
      this.$http.post(
        `${host}/pixels/${rgb[0]}/${rgb[1]}/${rgb[2]}`
      )
    },
    setState (value) {
      this.$http.post(
        `${host}/state/${value ? 'on' : 'off'}`
      )
    },
    onSaturationChange (saturation) {
      this.color.saturation = saturation
      this.setColor()
    },

    onLuminosityChange (luminosity) {
      this.color.luminosity = luminosity
      this.setColor()
    },
    onColorChange (hue) {
      this.color.hue = hue
      this.setColor()
    },
    onStateChange (state) {
      this.setState(state)
    }
  }
}
</script>

